import { useHistory, Route } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Modal from 'components/Modal/Modal';
import Searchbar from 'components/Searchbar/Searchbar';
import ImageInfo from 'components/ImageInfo/ImageInfo';
import Button from 'components/Button/Button';

const App = () => {
  const [showModal, setShowModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [page, setPage] = useState(1);
  const [src, setSrc] = useState('');
  const [alt, setAlt] = useState('');
  const [moreVisible, setMoreVisible] = useState(false);

  //                 
  const toggleModal = e => {
    if (!showModal) {
      setSrc(e.target.dataset.src);
      setAlt(e.target.alt);
      setShowModal(true);
    } else {
      setShowModal(false);
    }
  };

  //         
  const submitForm = value => {
    console.log(22222);
    setPage(1);
    setSearchQuery(value);
  };

  useEffect =
    (() => {
      history.push('/images/search');
    },
    [setSearchQuery]);

  //       " "
  const moreButtonRender = () => setMoreVisible(true);
  const moreButtonHide = () => setMoreVisible(false);

  //         " "
  function clickMoreButton() {
    setPage(page => page + 1);
  }
  const history = useHistory();
  return (
    <>
      <Searchbar onSubmit={submitForm} />
      <Route path="/images/search" exact>
        <>
          <ImageInfo
            searchQuery={searchQuery}
            page={page}
            onClick={toggleModal}
            moreButtonRender={moreButtonRender}
            moreButtonHide={moreButtonHide}
          />
          {moreVisible && <Button onClick={clickMoreButton} />}
        </>
      </Route>
    </>
  );
};

export default App;
